/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { ReactNode } from 'react'
import { useStaticQuery, graphql } from 'gatsby'

import { Header } from './header'
// tslint:disable-next-line: no-import-side-effect
import './layout.css'
import styled, { StyledComponent } from 'styled-components'
import { Footer } from './Footer'
import { theme } from '../data/theme'
import { MainNav } from './MainNav'

interface IChildren {
  children: ReactNode
}

interface ISiteQuery {
  site: {
    siteMetadata: {
      title: string
    }
  }
}

const StyledLayout: StyledComponent<'div', any, {}, never> = styled.div`
  background: ${theme.background};
  margin: 0 auto;
  max-width: 100vw;
  padding: 0;
  margin-bottom: 48em;
  z-index: 999;

  p: {
    text-align: justify;
  }
`

const StyledMain: StyledComponent<'main', any, {}, never> = styled.main`
  margin: 0 auto;
  padding: 4rem 1rem;

  a {
    text-decoration: none;
    color: ${theme.primary};

    & :hover {
      color: ${theme.primary_hover};
    }
  }

  @media screen and (max-width: 600px) {
    padding: 1rem;
  }
`

export const Layout: ({ children }: IChildren) => JSX.Element = ({
  children,
}: IChildren): JSX.Element => {
  // tslint:disable-next-line: no-void-expression
  const data: ISiteQuery = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.site.siteMetadata.title} />
      <StyledLayout>
        <StyledMain>{children}</StyledMain>
      </StyledLayout>
      <Footer />
      <MainNav />
    </>
  )
}
